<template>
  <div class="zhuinfo">
    <button type="button" class="baocun el-button el-button--success is-circle" @click="baocun">
      <!---->
      <i class="el-icon-check"></i>
      <!---->
    </button>
    <table>
      <tr style="height:68px">
        <td style="width:80px"></td>
        <td style="width:392px;">
          <el-input
            maxlength="32"
            @blur="cZhuyuanhao"
            style="width:235px;"
            v-model="yaopost.hospitalid"
            placeholder=""
          ></el-input>
          <br />
          <span class="idZhuyuanhao" v-show="zhuyuanhao" style="color:red;padding-left:74px;"></span>
        </td>
        <td style="width:100px"></td>
        <td style="width:392px">
          <!-- <el-input style="width:235px" v-model="yaopost.roomid" placeholder=""></el-input> -->

          <el-cascader style="width:235px;" v-model="roomif" :options="options"></el-cascader>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input maxlength="32" style="width:235px" v-model="yaopost.doctor" placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:68px">
        <td style="width:100px"></td>
        <td>
          <el-input maxlength="50" style="width:235px" v-model="yaopost.remark" placeholder=""></el-input>
        </td>
        <!-- <td style="width:100px"></td>
        <td>
          <el-select v-model="yaopost.isinhospital" placeholder="">
            <el-option
              v-for="item in zaiyuanstate"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>-->
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-date-picker
            style="width:235px"
            v-model="yaopost.admissiondate"
            type="date"
            placeholder=""
          ></el-date-picker>
        </td>
        <td></td>
        <td>
          <el-select style="width:235px;" v-model="yaopost.nursinglevel" placeholder="">
            <el-option
              v-for="item in grade"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:68px">
        <td style="width:100px"></td>
        <td>
          <el-select style="width:235px;" v-model="yaopost.health" placeholder="">
            <el-option
              v-for="item in Healthy"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="50"
            style="width:235px"
            v-model="yaopost.physicalcondition"
            placeholder=""
          ></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="50"
            style="width:235px"
            v-model="yaopost.carepoint"
            placeholder=""
          ></el-input>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="50"
            style="width:235px"
            v-model="yaopost.diettaboo"
            placeholder=""
          ></el-input>
        </td>
        <td></td>
        <td>
          <el-select style="width:235px;" v-model="yaopost.bloodtype" placeholder="">
            <el-option
              v-for="item in blood"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="50"
            style="width:235px"
            v-model="yaopost.drugreaction"
            placeholder=""
          ></el-input>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-input disabled style="width:235px" v-model="yaopost.code" placeholder=""></el-input>
        </td>
        <td></td>
        <td>
          <span></span>
          <el-input
            maxlength="8"
            size="mini"
            style="width:66px;margin-right:10px;"
            @blur="cXueyagao"
            v-model="yaopost.upperhypertension"
            placeholder=""
          ></el-input>
          <span></span>
          <el-input
            maxlength="8"
            size="mini"
            style="width:66px"
            @blur="cXueyagao"
            v-model="yaopost.lowerhypertension"
            placeholder=""
          ></el-input>
          <br />
          <span class="idXueyagao" v-show="xueyagao" style="color:red;padding-left:74px"></span>
        </td>
        <td style="width:100px"></td>
        <td>
          <span></span>
          <el-input
            maxlength="8"
            size="mini"
            style="width:66px;margin-right:10px;"
            @blur="cXueyadi"
            v-model="yaopost.upperhypotension"
            placeholder=""
          ></el-input>
          <span></span>
          <el-input
            maxlength="8"
            size="mini"
            style="width:66px"
            @blur="cXueyadi"
            v-model="yaopost.lowerhypotension"
            placeholder=""
          ></el-input>

          <span class="idXueyagao" v-show="xueyadi" style="color:red;padding-left:74px"></span>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="8"
            style="width:235px"
            @blur="cTiwengao"
            v-model="yaopost.uppertemperature"
            placeholder=""
          ></el-input>
          <br />
          <span class="idXueyagao" v-show="tiwengao" style="color:red;padding-left:74px"></span>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="8"
            style="width:235px"
            @blur="cTiwendi"
            v-model="yaopost.lowertemperature"
            placeholder=""
          ></el-input>
          <br />
          <span class="idXueyagao" v-show="tiwendi" style="color:red;padding-left:74px"></span>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="8"
            style="width:235px"
            @blur="cXinlvdi"
            v-model="yaopost.lowerheartrate"
            placeholder=""
          ></el-input>
          <br />
          <span class="idXueyagao" v-show="xinlvdi" style="color:red;padding-left:74px"></span>
        </td>
      </tr>
      <tr style="height:74px">
        <td style="width:100px"></td>
        <td>
          <el-input
            maxlength="8"
            style="width:235px"
            @blur="cXinlvgao"
            v-model="yaopost.upperheartrate"
            placeholder=""
          ></el-input>
          <br />
          <span class="idXueyagao" v-show="xinlvgao" style="color:red;padding-left:74px"></span>
        </td>

        <td style="width:100px"></td>
        <td></td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      options: [],
      zhuyuanhao: false,
      xueyagao: false,
      xueyadi: false,
      xinlvgao: false,
      xinlvdi: false,
      tiwengao: false,
      tiwendi: false,
      yaopost: {
        hospitalid: "",
        code: "",
        roomid: "",
        bedid: "",
        affiliateddoctor: "",
        remark: "",
        isinhospital: "",
        nursinglevel: "",
        health: "",
        physicalcondition: "",
        carepoint: "",
        diettaboo: "",
        bloodtype: "",
        drugreaction: "",
        id: this.did,
        admissiondate: "",
        upperhypertension: "",
        lowerhypertension: "",

        upperhypotension: "",
        lowerhypotension: "",

        uppertemperature: "",
        lowertemperature: "",
        upperhartrate: "",
        lowerhartrate: "",
      },
      // 
      roomif: [],
      grade: [
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
      ],
      Healthy: [
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
      ],
      zaiyuanstate: [
        {
          value: "0",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "1",
          label: "",
        },
      ],
      blood: [
        {
          value: "A",
          label: "A",
        },
        {
          value: "B",
          label: "B",
        },
        {
          value: "O",
          label: "O",
        },
        {
          value: "AB",
          label: "AB",
        },
        {
          value: "RhA",
          label: "RhA",
        },
        {
          value: "RhB",
          label: "RhB",
        },
        {
          value: "RhO",
          label: "RhO",
        },
        {
          value: "RhAB",
          label: "RhAB",
        },
      ],
      xinyaopost: "",
    };
  },
  props: {
    did: null,
  },
  created() {
    console.log("");
    // 
    this.$ajax
      .post(
        process.env.API_HOST + "/bed/getNoPersonBedTree",
        `personId=${this.did}`
      )
      .then((respones) => {
        this.options = respones.data;
      });
    // 
    this.$ajax
      .post(process.env.API_HOST + "/person/getPersonById", `id=${this.did}`)
      .then((respones) => {
        this.yaopost = respones.data.prnPerson;
        this.roomif = respones.data.positionArr;
      });
  },
  watch: {
    yaopost: {
      handler(newName, oldName) {
        this.xinyaopost = newName;
      },
      deep: true,
    },
    did(val) {
      // 
      this.$ajax
        .post(
          process.env.API_HOST + "/bed/getNoPersonBedTree",
          `personId=${this.did}`
        )
        .then((respones) => {
          this.options = respones.data;
        });
      // 
      this.$ajax
        .post(process.env.API_HOST + "/person/getPersonById", `id=${this.did}`)
        .then((respones) => {
          console.log(respones.data);
          this.yaopost = respones.data.prnPerson;
          this.roomif = respones.data.positionArr;
        });
    },
  },
  methods: {
    cZhuyuanhao() {
      var zyh = /^\d*\.?\d+$/;
      if (
        zyh.test(this.yaopost.hospitalid) ||
        this.yaopost.hospitalid == "" ||
        this.yaopost.hospitalid == undefined
      ) {
        this.zhuyuanhao = false;
      } else {
        this.zhuyuanhao = true;
      }
    },
    cXueyagao() {
      var xyg = /^\d*\.?\d+$/;
      if (
        (xyg.test(this.yaopost.upperhypertension) ||
          this.yaopost.upperhypertension == "" ||
          this.yaopost.upperhypertension == undefined) &&
        (xyg.test(this.yaopost.lowerhypertension) ||
          this.yaopost.lowerhypertension == "" ||
          this.yaopost.lowerhypertension == undefined)
      ) {
        this.xueyagao = false;
      } else {
        this.xueyagao = true;
      }
    },
    cXueyadi() {
      var xyd = /^\d*\.?\d+$/;
      if (
        (xyd.test(this.yaopost.upperhypotension) ||
          this.yaopost.upperhypotension == "" ||
          this.yaopost.upperhypotension == undefined) &&
        (xyd.test(this.yaopost.lowerhypotension) ||
          this.yaopost.lowerhypotension == "" ||
          this.yaopost.lowerhypotension == undefined)
      ) {
        this.xueyadi = false;
      } else {
        this.xueyadi = true;
      }
    },
    cTiwengao() {
      var twg = /^\d*\.?\d+$/;
      if (
        twg.test(this.yaopost.uppertemperature) ||
        this.yaopost.uppertemperature == "" ||
        this.yaopost.uppertemperature == undefined
      ) {
        this.tiwengao = false;
      } else {
        this.tiwengao = true;
      }
    },
    cTiwendi() {
      var twd = /^\d*\.?\d+$/;
      if (
        twd.test(this.yaopost.lowertemperature) ||
        this.yaopost.lowertemperature == "" ||
        this.yaopost.lowertemperature == undefined
      ) {
        this.tiwendi = false;
      } else {
        this.tiwendi = true;
      }
    },
    cXinlvgao() {
      var xlg = /^\d*\.?\d+$/;
      if (
        xlg.test(this.yaopost.upperheartrate) ||
        this.yaopost.upperheartrate == "" ||
        this.yaopost.upperheartrate == undefined
      ) {
        this.xinlvgao = false;
      } else {
        this.xinlvgao = true;
      }
    },
    cXinlvdi() {
      var xld = /^\d*\.?\d+$/;
      if (
        xld.test(this.yaopost.lowerheartrate) ||
        this.yaopost.lowerheartrate == "" ||
        this.yaopost.lowerheartrate == undefined
      ) {
        this.xinlvdi = false;
      } else {
        this.xinlvdi = true;
      }
    },
    baocun() {
      if (
        this.zhuyuanhao == false &&
        this.xueyagao == false &&
        this.xueyadi == false &&
        this.xinlvgao == false &&
        this.xinlvdi == false &&
        this.tiwengao == false &&
        this.tiwendi == false
      ) {
        this.yaopost.roomid = this.roomif[3];
        this.yaopost.bedid = this.roomif[4];
        this.$ajax
          .post(
            process.env.API_HOST + "/person/updatePerson",
            `person=${JSON.stringify(this.xinyaopost)}&userid=${
              this.$store.state.token
            }`
          )
          .then((respones) => {
            console.log(respones.data);
            if (respones.data.state == "success") {
              this.$message({
                type: "success",
                message: "",
              });
              // 
              // this.$ajax
              //   .post(process.env.API_HOST + "/bed/getNoPersonBedTree")
              //   .then(respones => {
              //     this.options = respones.data;
              //   });
              // 
              this.$store.dispatch("changecardfield", this.did);
              this.$store.dispatch("person_getPersonList", {
                id: "",
                name: "",
                sex: "",
                roomN: "",
                state: "",
                hasDevice: "",
              });
              // 
              if (this.$store.state.hospitalshow) {
                this.$store.state.hospitalshowactive = true;
                this.$store.state.activeNames = "5";
                this.$store.state.actives = 5;
              }
            } else {
              this.$message({
                type: "warning",
                message: respones.data.msg,
              });
            }
          });
      } else {
        this.$message({
          type: "warning",
          message: "",
        });
      }
    },
  },
};
</script>
<style>
.zhuinfo table {
  border: none;
}
.baocun {
  position: absolute;
  right: 10px;
  bottom: 10px;
  z-index: 999;
}
</style>




